@import '../main.scss';
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;700;900&display=swap');

html, body{
    height: 100%;
    margin: 0;
}
body{
    display: flex;
    align-content: center;
    align-items: center;
    flex-direction: row;
    position: relative;
    
    font-family: 'Inter', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 32px;
    color: #FFFFFF;

    background: linear-gradient(213.91deg, #0D69BE 11.63%, #0B5CA7 84.05%);

    /* only mobile */
    @media #{$break1} {
        display: inline;
        background: -moz-linear-gradient(90deg, rgba(13,105,190,1) 0%, rgba(11,92,167,1) 100%);        
        background: -webkit-linear-gradient(90deg, rgba(13,105,190,1) 0%, rgba(11,92,167,1) 100%);
        background: -o-linear-gradient(90deg, rgba(13,105,190,1) 0%, rgba(11,92,167,1) 100%);
        background: -ms-linear-gradient(90deg, rgba(13,105,190,1) 0%, rgba(11,92,167,1) 100%);
        background: linear-gradient(90deg, rgba(13,105,190,1) 0%, rgba(11,92,167,1) 100%);
    }
}