@import '../../../main.scss';

.odd{ 
    flex: 0 0 560px;
    height: 636px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;


    .bottom{
        border: 1px solid #268AE5;
        padding: 51px 0px 45px 45px;
        
        a.phone{
            background-image: url(/images/phone.png);
            background-repeat: no-repeat;
            display: block;
            padding-left: 71px;
            line-height: 56px;
            margin-bottom: 15px;
        }

    }


    .order{
        display: none;
    }

    @media #{$break1} {
        
        flex: auto;
        height: auto;

        .top{
            margin-bottom: 30px;
        }

        .order{
            display: inline;
            font-weight: 700;
            font-size: 18px;
            line-height: 66px;
            background: linear-gradient(270deg, #E9A82B 0%, #CE760E 100%);
            box-shadow: 0px 2px 25px rgba(12, 36, 58, 0.28);
            border-radius: 5px;
            text-align: center;
            margin-bottom: 50px;
        }

        .bottom{
            display: none;
        }
    }
}

