@import '../main.scss';

h1{    
    font-weight: 900;
    font-size: 52px;
    line-height: 63px;
    margin: 0 0 25px 0;
}

h2{
    font-weight: 900;
    font-size: 34px;
    line-height: 44px;
    margin: 0 0 40px 0;
}

h3{
    font-size: 20px;
    line-height: 32px;
    margin-bottom: 0;
}

a{    
    font-weight: 700;
    font-size: 30px;
    line-height: 36px;
    text-decoration: none;
    color: #fff;
}

p{
    margin: 0;
}

@media #{$break1} {
    h1{
        font-size: 30px;
        line-height: 40px;
    }

    p{
        font-size: 18px;
        line-height: 26px;
    }
}