@import '../../../main.scss';

.container{ 
    display: flex;    
    justify-content: space-between;
    align-items: center;
    
    margin-right: auto;
    margin-left: auto; 
    
    z-index: 10;
	
    /* only mobile */
    @media #{$break1}{
        flex-direction: column;        
        width: auto;
        padding: 85px 20px;
    }	

    /* table */
    @media #{$break2}{	
        width: 100%;
        padding: 85px 20px;
    }

    /* desktop */
    @media #{$break3}{	
        width: 1140px;
    }
}